.skill-description {
  font-weight: lighter;
}
.skill-names {
  /* font-family: "Josefin Sans", sans-serif; */
  font-weight: 550;
}

.cv-timeline-row {
    page-break-inside: avoid;
    overflow: hidden;
    display:flex;

}

.cv-timeline-side {
    float: left;
    width: 17%;
    text-align: right;
    padding-right: 15px;
    color: #777;
    font-size: 13px;
}

.cv-timeline-company {
    text-transform: uppercase;
}

a {
    text-decoration: none;
}

.red, a {
    color: #BB1E1E;
}
a {
    background: 0 0;
}
.cv-timeline-body {
    float: left;
    width: 70%;

}
.cv-timeline-position {
    font-weight: 400;
    font-size: 18px;
    color: #333;
    line-height: 1.3;
}

.cv-timeline-body p, .cv-timeline-body ul {
    margin-top: 5px;
    margin-bottom: 5px;
}
.tou-node {
    all: unset;
    will-change: opacity;
    transition-property: opacity;
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
    opacity: 1;
}
.cv-timeline-body p, .cv-timeline-body ul {
    margin-top: 5px;
    margin-bottom: 5px;
}
.cv-section-content ol, .cv-section-content ul:not(.tech-tags) {
    padding-left: 20px;
}
ul {
    display: block;
    list-style-type: disc;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    -webkit-padding-start: 40px;
            padding-inline-start: 40px;
}
li {
    display: list-item;
    text-align: -webkit-match-parent;
}
*, :after, :before {
    box-sizing: inherit;
}

@media screen and (min-width: 768px){
.cv-timeline-company {
    background-color: #eee;
    padding: 3px 7px;
    margin-bottom: 5px;
    border-radius: 2px;
}

}
